header {
  padding: 0.8rem;
}

.profileImage {
  position: relative;
}

.profileImage img {
  width: 40vw;
  display: flex;
}

.profileImage::before {
  content: "";
  position: absolute;
  inset: -10px;
  border: 4px solid #b580d1;
  border-top-color: #e9edc9;
  border-bottom-color: #e9edc9;
  border-radius: 50%;
  z-index: -1;
  animation: spin 3s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

header section h1 {
  width: 100%;
}

header section h2 {
  border-top: 0.2rem solid #b580d1;
  padding-top: 0.8rem;
}

@media screen and (min-width: 768px) {
  header section h1 {
    width: auto;
  }

  .profileImage img {
    width: 15vw;
    display: flex;
  }

  header section h1 {
    border: 0.2rem solid #b580d1;
    border-radius: 5rem 0 0 5rem;
    padding: 1rem;
  }

  header section h2 {
    border: 0.2rem solid #b580d1;
    border-radius: 0 5rem 5rem 0;
    padding: 1rem;
  }

  .info {
    margin-left: 2rem;
  }
}
